export class World {
	constructor(rows, columns, map) {
		this.rows = rows;
		this.columns = columns;
		this.tileSize = map.width;
		this.width = columns * this.tileSize;
		this.height = rows * this.tileSize;
		this.sprite = map;

		this.map = [];
		this.rendered = document.createElement('canvas');
		this.rendered.width = this.width;
		this.rendered.height = this.height;

		let context = this.rendered.getContext('2d');

		for (let row = 0; row < rows; row++) {
			this.map[row] = [];
			for (let column = 0; column < columns; column++) {
				this.map[row][column] = Math.random() < 0.1 ? Math.floor(Math.random() * 5) : 0;
				context.drawImage(
					this.sprite.getFrame(this.map[row][column]),
					column * this.tileSize,
					row * this.tileSize
				)
			}
		}
	}

	getIndex(coordinate) {
		return Math.floor(coordinate / this.tileSize);
	}

	isTileNotWalkable(x, y) {
		const row = this.getIndex(y);
		const column = this.getIndex(x);

		return this.map[row] && this.map[row][column] > 0;
	}
}


