import * as Loader from './loader';
import { World } from './world';
import { Keyboard } from './keyboard';
import { Camera } from './camera';
import { Player } from './player';
import { Mouse } from './mouse';
import { Sprite } from './sprite';
import { Enemy } from './enemy';
import { KEYS } from './constants/keys';
import { INVERTORY } from './constants/invertory';
import { Weapon } from './weapon';
import { RESOURCES } from './constants/resources';
import { CHARACTERS } from './constants/characters';
import { SPRITES } from './constants/sprites';

export class Game {
	constructor(context, audioContext) {
		this.context = context;
		this.audioContext = audioContext;
		this.loopedSounds = {};
		this.framesCount = 0;
	}

	init() {
		this.bulletHoles = [];
		this.blood = [];
		this.weapons = [];
		this.enemies = [];
		this.score = 0;
		this.world = new World(30, 30, Loader.getSprite('map'));
		this.keyboard = new Keyboard(KEYS);
		this.mouse = new Mouse();
		this.player = new Player(this.world, CHARACTERS.player);
		this.camera = new Camera(this.world);
		this.camera.follow(this.player);

		this.enemyInterval = setInterval(() => {
			if (!this.isPaused)  {
				this.enemies.push(new Enemy(this.world, this.player, CHARACTERS.rat));
				console.log('Generated enemy');
			}
		}, 3000);
		this.weaponInterval = setInterval(() => {
			if (!this.isPaused) {
				const probability = Math.random();
				if (this.score >= 3000 && probability < 0.5) {
					this.weapons.push(new Weapon(Loader.getSprite('assaultRifle'), Loader.getSprite('assaultRifleBackward'), INVERTORY.assaultRifle));
				} else if (this.score >= 1000 && probability < 0.5) {
					this.weapons.push(new Weapon(Loader.getSprite('shotgun'), Loader.getSprite('shotgunBackward'), INVERTORY.shotgun));
				} else {
					this.weapons.push(new Weapon(Loader.getSprite('pistol'), Loader.getSprite('pistolBackward'), INVERTORY.pistol));
				}
				this.weapons[this.weapons.length - 1].x = Math.floor(Math.random() * this.world.width);
				this.weapons[this.weapons.length - 1].y = Math.floor(Math.random() * this.world.height);

				console.log('Generated weapon');
			}
		}, 10000);

		this._previousElapsed = Date.now();
	}

	update(delta) {
		let dirx = 0, diry = 0, moved;

		if (this.keyboard.isDown(KEYS.left)) { dirx = -1; }
		else if (this.keyboard.isDown(KEYS.right)) { dirx = 1; }

		if (this.keyboard.isDown(KEYS.up)) { diry = -1; }
		else if (this.keyboard.isDown(KEYS.down)) { diry = 1; }

		this.player.mousex = this.mouse.x;
		this.player.mousey = this.mouse.y;

		this.player.sprite.update(delta);
		moved = this.player.move(delta, dirx, diry);

		if ((dirx || diry) && !this.loopedSounds['trot'] && (moved.x || moved.y)) {
			this.playLoopedSound(Loader.getSound('sound/trot.mp3'), 'trot');
		} else if (((!dirx && !diry) || (!moved.x && !moved.y)) && this.loopedSounds['trot']) {
			this.stopLoopedSound('trot');
		}

		for (let weapon of this.weapons) {
			if (weapon.x >= this.player.x &&
				weapon.x <= this.player.x + this.player.sprite.width &&
				weapon.y >= this.player.y &&
				weapon.y <= this.player.y + this.player.sprite.height) {
				if (this.player.invertory[weapon.name]) {
					this.player.invertory[weapon.name].bullets += weapon.bullets;
				} else {
					this.player.invertory[weapon.name] = weapon;
					this.player.currentItem = weapon;
				}

				this.playSound(Loader.getSound('sound/pickup.mp3'));

				this.weapons.splice(this.weapons.indexOf(weapon), 1);
			}
		}

		if (this.keyboard.isDown(KEYS.pistol) && this.player.invertory.pistol && this.player.invertory.pistol.bullets && (!this.player.currentItem || this.player.currentItem.name !== 'pistol')) {
			this.player.currentItem = this.player.invertory.pistol;
			this.playSound(Loader.getSound('sound/pickup.mp3'));
		}

		if (this.keyboard.isDown(KEYS.shotgun) && this.player.invertory.shotgun && this.player.invertory.shotgun.bullets && (!this.player.currentItem || this.player.currentItem.name !== 'shotgun')) {
			this.player.currentItem = this.player.invertory.shotgun;
			this.playSound(Loader.getSound('sound/pickup.mp3'));
		}

		if (this.keyboard.isDown(KEYS.assaultRifle) && this.player.invertory.assaultRifle && this.player.invertory.assaultRifle.bullets && (!this.player.currentItem || this.player.currentItem.name !== 'assaultRifle')) {
			this.player.currentItem = this.player.invertory.assaultRifle;
			this.playSound(Loader.getSound('sound/pickup.mp3'));
		}

		let isEnemyHinted = false, isPlayerHinted, hitX, hitY;

		if (this.player.currentItem) {
			if (!this.player.currentItem.onReload) {
				this.player.currentItem.onReload = () => this.playSound(Loader.getSound('sound/' + this.player.currentItem.name + 'Reload.mp3'))
			}
			if (!this.player.currentItem.availableBullets && !this.player.currentItem.bullets) {
				this.player.invertory[this.player.currentItem.name] = null;
				this.player.currentItem = null;
			} else {
				if (this.keyboard.isDown(KEYS.reload)) {
					this.player.currentItem.reload();
				}

				if (this.mouse.isClicked && !this.player.currentItem.isShoted && !this.player.currentItem.isReloading) {
					if (this.player.currentItem.shot()) {
						hitX = this.camera.x + this.mouse.x;
						hitY = this.camera.y + this.mouse.y;
						console.log(this.player.currentItem.availableBullets  + ' / '  + this.player.currentItem.bullets);
						this.isFlameAppeared = true;
						setTimeout(() => this.isFlameAppeared = false, 50);
						this.playSound(Loader.getSound('sound/' + this.player.currentItem.name + '.mp3'));
					}
				} else if (!this.mouse.isClicked) { // We also have non-automatic weapons (pistol, shotgun)
					this.player.currentItem.isShoted = false;
				}
			}
		}

		// Update enemies state
		for (let enemy of this.enemies) {
			enemy.sprite.update(delta);

			if (enemy.attackPlayer(delta) && !isPlayerHinted) {
				this.playSound(Loader.getSound('sound/' + enemy.name + 'Attack.mp3'));
				isPlayerHinted = true;
			}

			if (hitX && hitY) {
				if (hitX >= enemy.x && hitX <= enemy.x + enemy.sprite.width &&
					hitY >= enemy.y && hitY <= enemy.y + enemy.sprite.height) {
					enemy.health -= this.player.currentItem.damage;
					isEnemyHinted = true;

					console.log('Enemy health - ' + enemy.health);
					if (enemy.health <= 0) {
						this.playSound(Loader.getSound('sound/' + enemy.name + 'Death.mp3'));
						this.enemies.splice(this.enemies.indexOf(enemy), 1);
						this.score += enemy.award;
						console.log('Score - ' + this.score);
					}

					break;
				}
			}
		}

		if (isPlayerHinted) {
			let blood = new Sprite(SPRITES.blood);
			blood.index = Math.random() * blood.framesCount;
			this.blood.push({
				x: this.player.x + this.player.sprite.width / 2,
				y: this.player.y + this.player.sprite.height / 2,
				sprite: blood
			});
			setTimeout(() => {
				this.blood.splice(this.blood.indexOf(blood), 1);
			}, 10000)
		}

		if (hitX && hitY) {
			if (isEnemyHinted) {
				let blood = new Sprite(SPRITES.blood);
				blood.index = Math.random() * blood.framesCount;
				this.blood.push({
					x: hitX,
					y: hitY,
					sprite: blood
				});
				setTimeout(() => {
					this.blood.splice(this.blood.indexOf(blood), 1);
				}, 10000)
			} else {
				let bulletHole = {
					x: hitX,
					y: hitY,
					sprite: Loader.getSprite('hole')
				};
				this.bulletHoles.push(bulletHole);
				setTimeout(() => {
					this.bulletHoles.splice(this.bulletHoles.indexOf(bulletHole), 1);
				}, 10000)
			}
		}

		this.camera.update();

		if (this.keyboard.isDown(KEYS.pause)) {
			this.pause();
		}

		if (this.player.health <= 0) {
			return this.gameOver();
		}
	}

	render() {
		// Render map
		this.context.drawImage(
			this.world.rendered,
			-this.camera.x,
			-this.camera.y
		);

		// Render blood
		for (const blood of this.blood) {
			if (this.camera.isInBounds(blood.x, blood.y)) {
				this.context.drawImage(
					blood.sprite.getFrame(blood.sprite.index),
					Math.floor(blood.x - this.camera.x - blood.sprite.width / 2),
					Math.floor(blood.y - this.camera.y - blood.sprite.height / 2),
				)
			}
		}

		// Render bullet holes
		for (const hole of this.bulletHoles) {
			if (this.camera.isInBounds(hole.x, hole.y)) {
				this.context.drawImage(
					hole.sprite.getFrame(0),
					Math.floor(hole.x - this.camera.x - hole.sprite.width / 2),
					Math.floor(hole.y - this.camera.y - hole.sprite.height / 2)
				)
			}
		}

		// Render weapons
		for (const weapon of this.weapons) {
			if (this.camera.isInBounds(weapon.x, weapon.y)) {
				this.context.drawImage(
					weapon.sprite.getFrame(0),
					Math.floor(weapon.x - this.camera.x - weapon.sprite.width / 2),
					Math.floor(weapon.y - this.camera.y - weapon.sprite.height / 2),
				)
			}
		}

		// Render player
		this.context.drawImage(
			this.player.sprite.getFrame(this.player.sprite.index, this.player.state),
			this.player.screenX,
			this.player.screenY
		);

		// Render enemies
		for (let enemy of this.enemies) {
			if (this.camera.isInBounds(enemy.x, enemy.y) || this.camera.isInBounds(enemy.x + enemy.sprite.width, enemy.y + enemy.sprite.height)) {
				this.context.drawImage(
					enemy.sprite.getFrame(enemy.sprite.index, enemy.state),
					enemy.x - this.camera.x,
					enemy.y - this.camera.y,
				);
			}
		}

		// Render player's weapon
		if (this.player.currentItem && !this.player.currentItem.isReloading) {
			this.context.drawImage(
				this.player.currentItem.sprite.getFrame(0),
				this.player.direction > 0 ? this.player.screenX + this.player.sprite.width : this.player.screenX - this.player.currentItem.sprite.width,
				this.player.screenY + 35,
			);
		}

		// Render weapon fire
		if (this.player.currentItem && this.isFlameAppeared) {
			this.context.drawImage(
				this.player.currentItem.fire.getFrame(0, this.player.direction > 0 ? 0: 1),
				this.player.direction > 0 ?
					this.player.screenX + this.player.sprite.width + this.player.currentItem.sprite.width :
					this.player.screenX - this.player.currentItem.sprite.width - 50,
				this.player.screenY + 23,
			);
		}

		// Render HUD
		this.context.rect(70, window.innerHeight - 100, 400, 25);

		// Health bar
		this.context.strokeStyle = '#14ff17';
		this.context.fillStyle = '#14ff17';
		this.context.stroke();
		this.context.fillRect(70, window.innerHeight - 100, 400 * (this.player.health / 100), 25);

		// Text style
		this.context.font='36px Overseer';
		this.context.textAlign='center';

		// Score
		this.context.fillText('Score: ' + this.score, Math.floor(window.innerWidth / 2), window.innerHeight - 75);

		// Weapon
		if (this.player.currentItem) {
			this.context.fillText(this.player.currentItem.availableBullets + ' / ' + this.player.currentItem.bullets, window.innerWidth - 100, window.innerHeight - 75);
		}
	}

	run() {
		this._previousElapsed = 0;
		Loader.onprogress(progress => {
			console.log(progress.percentage);
			document.getElementById('loadingBar').style.width = progress.percentage + '%';
		});
		Loader.onload(() => {
			this.playLoopedSound(Loader.getSound('sound/background.mp3'), 'background', 0.2);
			document.getElementById('loading').style.display = 'none';
			document.getElementById('menu').style.display = 'block';

			document.getElementById('mainImage').appendChild(Loader.getImage('img/main.png'));

			document.getElementById('start').onclick = () => {
				document.getElementById('main').style.display = 'none';
				this.init();
				window.requestAnimationFrame(this.tick.bind(this));
			}
		});
		Loader.loadResources(RESOURCES, this.audioContext, SPRITES);
	}

	tick() {
		if (!this.isPaused) {
			window.requestAnimationFrame(this.tick.bind(this));

			// clear previous frame
			this.context.clearRect(0, 0, window.innerWidth, window.innerHeight);

			// compute delta time in seconds -- also cap it
			let now = Date.now();
			let delta = (now - this._previousElapsed) / 1000;
			delta = Math.min(delta, 0.25); // maximum delta of 250 ms
			this._previousElapsed = now;

			this.update(delta);
			this.render();
		}
	}

	playSound(buffer) {
		const source = this.audioContext.createBufferSource(); // creates a sound source
		source.buffer = buffer; // tell the source which sound to play
		source.connect(this.audioContext.destination); // connect the source to the context's destination (the speakers)
		source.start(0);
	}

	playLoopedSound(buffer, name, volume = 1) {
		const source = this.audioContext.createBufferSource(); // creates a sound source
		source.buffer = buffer; // tell the source which sound to play
		source.loop = true;
		const gainNode = this.audioContext.createGain(); // Connect the source to the gain node.
		source.connect(gainNode); // Connect the gain node to the destination.
		gainNode.connect(this.audioContext.destination);
		gainNode.gain.value = volume;

		this.loopedSounds[name] = source;

		source.start(0);
	}

	stopLoopedSound(name) {
		this.loopedSounds[name].stop();
		this.loopedSounds[name] = null;
	}

	pause() {
		this.isPaused = true;

		if (this.loopedSounds['trot']) {
			this.loopedSounds['trot'].stop();
			this.loopedSounds['trot'] = null;
		}

		document.getElementById('pause').style.display = 'block';
		document.getElementById('pauseImage').appendChild(Loader.getImage('img/pause.png'));
		document.getElementById('resume').onclick = this.unpause.bind(this);
	}

	unpause() {
		document.getElementById('pause').style.display = 'none';
		this.isPaused = false;
		window.requestAnimationFrame(this.tick.bind(this));
	}

	gameOver() {
		console.log('Game over');
		this.isPaused = true;
		clearInterval(this.enemyInterval);
		clearInterval(this.weaponInterval);

		if (this.loopedSounds['trot']) {
			this.loopedSounds['trot'].stop();
			this.loopedSounds['trot'] = null;
		}

		document.getElementById('gameOver').style.display = 'block';
		document.getElementById('gameOverImage').appendChild(Loader.getImage('img/gameOver.png'));
		document.getElementById('score').innerText= this.score;
		document.getElementById('tryAgain').onclick = this.reset.bind(this);
	}

	reset() {
		document.getElementById('gameOver').style.display = 'none';

		this.init();
		this.isPaused = false;
		window.requestAnimationFrame(this.tick.bind(this));
	}
}


