export class Mouse {
	constructor() {
		this.x = 0;
		this.y = 0;
		window.addEventListener('mousemove', event => {
			this.x = event.clientX;
			this.y = event.clientY;
		});
		window.addEventListener('mousedown', () => {
			this.isClicked = true;
		});
		window.addEventListener('mouseup', () => {
			this.isClicked = false;
		});
		window.addEventListener('blur', () => {
			this.isClicked = false;
		});
	}
}



