import { Character } from './character';
import { Sprite } from './sprite';
import { STATES as STATE } from './constants/states';
import { Weapon } from './weapon';
import { INVERTORY } from './constants/invertory';
import * as Loader from './loader';

export class Player extends Character {
	constructor(world, config) {
		super(world.width / 2, world.height / 2, world, config.default);
		this.defaultSprite = this.sprite;
		this.activeSprite = new Sprite(config.active); // lightning unicorn
		this.screenX = this.x;
		this.screenY = this.y;

		this.invertory = {
			pistol: new Weapon(Loader.getSprite('pistol'), Loader.getSprite('pistolBackward'), INVERTORY.pistol)
		};
		this.currentItem = this.invertory.pistol;
	}

	_updateState(x, y) {
		this.sprite = this.currentItem ? this.activeSprite : this.defaultSprite;
		this.direction = this.mousex > this.screenX + (this.sprite.width / 2) ? 1 : -1;

		if (this.currentItem) {
			this.currentItem.sprite = this.direction > 0 ? this.currentItem.spriteForward : this.currentItem.spriteBackward;
		}

		if (x || y) {
			this.state = this.direction === 1 ? STATE.moveForward : STATE.moveBackward;
			if (x * this.direction * this.sprite.animationSpeed < 0) {
				this.sprite.animationSpeed = -this.sprite.animationSpeed;
			}
		}

		if (!x && !y) {
			this.state = this.direction === 1 ? STATE.idleForward : STATE.idleBackward;
		}
	}
}


